#optionsFieldsets {
  display: flex;
  flex-direction: row;
  gap: 0;

  fieldset {
    border: none;
    padding: 0;
  }

  fieldset + fieldset {
    padding-left: 40px;
  }

  h2 {
    margin-bottom : 1em;
  }

  label:hover {
    text-decoration: underline;
  }
}

.attribute {
  text-align: left;
  font-size: 1.5em;
  position: relative;
}

.attribute + .attribute {
  margin-top: 5px;
}

.buttonContainer {
  margin-top: 2em;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 10px;

  button {
    padding: 5px 10px;
  }
}

.countHolder {
  margin-top: 2em;
}

#go {
  margin-top: 2em;
}

// checkboxes inspired by GOV.UK Design System - https://github.com/alphagov/govuk-frontend

input[type="checkbox"] {
  font-size: 20px;
  cursor: pointer;
  position: absolute;
  z-index: 1;
  top: -2px;
  left: -2px;
  width: 44px;
  height: 44px;
  margin: 0;
  opacity: 0;
}

label {
  display: inline-block;
  margin-bottom: 0;
  padding: 0;
  padding-left: 38px;
  cursor: pointer;
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 5px;
    left: 0;
    width: 24px;
    height: 24px;
    border: 2px solid currentColor;
    border-radius: 5px;
    background: transparent;
  }

  &::after {
    content: "";
    position: absolute;
    top: 13px;
    left: 7px;
    width: 11px;
    height: 5px;
    transform: rotate(-45deg);
    border: solid;
    border-width: 0 0 3px 3px;
    border-top-color: transparent;
    opacity: 0;
    background: transparent;
  }
}

input[type="checkbox"]:checked+label::after {
  opacity: 1;
}

input[type="checkbox"]:focus+label::before {
  outline: orange 2px auto;
}
